body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

p + p {
  margin-top: 0;
}

img {
    max-height: 50px;
    max-width: 50px;
    margin-right: 10px;
    margin-left:10px;
    margin-top: 3px;
    margin-bottom: 3px;
}

.header {
    background-color: #73638F;
    padding: 0px 0px 0px 0px;
}

.header a:link, a:visited, a:active {
    color: white;
    padding-left: 10px;
    padding-right: 10px;
}
.header a:hover {
    color: #cfcfcf;
    text-decoration: none;
    padding-left: 10px;
    padding-right: 10px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.btn-savechanges {
    color: white;
    background: blueviolet;
    border: none;
    border-radius: 5px;
    width: 170px;
    margin: 5px 0px 0px 0px;
}

.team-sprite{
    max-height: 40px;
    max-width: 40px;
    margin: 0px -4px 0px 0px;
}

.btn-newteam {
    color: white;
    background: blueviolet;
    border: none;
    border-radius: 5px;
    width: 170px;
    margin: 5px;
}

.teambuilder {
    overflow: hidden;
    margin-top: 0px 10px 10px 10px;
    display: flex;
    height: 90vh;
    max-height: 90vh;
    justify-content: center;
}

.team-wrapper {
    direction: ltr;
    overflow: hidden;
    background-color: #f0e9f7;
    /* box-shadow: 0px 4px 7px -5px rgba(35,0,85,0.64); */
    font-size: 10px;
    display: flex;
    flex-direction: row;
    margin: 5px;
    border-radius: 10px;
    width: 250px;
    min-height: 53px;
    height: 53px
}

.team-wrapper:hover {
    cursor: pointer;
}

.teamview {
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
    background-color: #cabfe0;
    border-radius: 10px;
    margin: 10px;
    padding: 5px;
    align-items: stretch;
    display: flex;
    overflow-y: auto;
    direction: rtl;
    height: 95%;
    min-height: 95%;
    flex-direction: column;
    /* justify-content: flex-end; */
}

