.setview {
    background-color: #cabfe0;
    border-radius: 10px;
    margin: 10px;
    padding: 5px 10px 10px 5px;
    height: 95%;
    min-height: 95%;
    width: 100%;
    overflow-y: auto;
    align-items: stretch;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}

.set-tabs-wrapper {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* border-left: 1px solid purple; */
}

.btn-newset {
    display: flex;
    align-items: center;
    height: 10%;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    background-color: rgba(0, 0, 0, 0.0);
    border-style: none;
    font-size: 25px;
    color: blueviolet;
}

.btn-newset:focus {
    outline: none;
  }

.set-tab {
    z-index: 1;
    height: 15%;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: flex;
    align-items: center;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    /* border-right: 1px solid purple; */
}

.set-tab:hover {
    cursor: pointer;
}

.picker-wrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    border-radius: 10px;
}

.picker-header-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    border-radius: 10px;
}

.picker-tabs-wrapper {
    display: flex;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    flex-direction: row;
    justify-content: center;
    height: fit-content;
    border-radius: 10px;
    width: 100%
}

.picker-tab {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 5px 10px 0px 10px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.picker-tab:hover {
    cursor: pointer;
}

.picker-content-wrapper {
    z-index: 10;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    background-color: #f0e9f7;
    border-radius: 10px;
}

.set-wrapper {
    font-size: x-small;
    min-height: -webkit-fit-content;
    min-height: -moz-fit-content;
    min-height: fit-content;
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
    max-width: -webkit-fit-content;
    max-width: -moz-fit-content;
    max-width: fit-content;
    display: flex;
    flex-direction: column;
    border-color: black;
    border-style: bold;
    padding: 5px;
}



.set-sprite {
    min-height: 110px;
    min-width: 110px;
    margin: -5px;
    padding: 0px 0px 0px 0px;
}

.set-field-area {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.set-field {
    box-shadow: 0px 0px 2px -1px #1454f7;
    border-radius: 4px;
    width: 130px;
    margin: 3px 0px 3px 3px;
    font-size: small;
    border-style: none;
    height: 20px
}

.set-stat-box {
    margin: 0px 0px 3px 3px; 
    width: inherit;
    height: 100%;
    background-color: white;
    border-radius: 4px;
    box-shadow: 0px 0px 2px -1px #1454f7;
}

