.type-icon {
    display: inline-block;
    width: 60px;
    margin-bottom: 2px;
    border: 1px solid #a3a3a3;
    border-radius: 5px;
    border: 1px solid rgba(0,0,0,0.2);
    color: #fff;
    font-size: .75rem;
    font-weight: normal;
    line-height: 1.3rem;
    text-align: center;
    text-shadow: 0px 0px 4px rgba(0,0,0,0.7);
    text-transform: uppercase;
    transition: opacity .4s;
}


